import React from "react";
import PropTypes from 'prop-types';

// structuring the card/holder
const Card = ({ property }) => {
    const { index, picture, details, name } = property;
    return ( 
        <div id = { `card-${index}` } className = "card" >
            <img src = { picture } alt = { name } className="image-moves" />
            <div className="card-details">
                <div className="aboutImage">
                <h1>{name}</h1>
                {details}
            </div>
        </div>        
    </div>
    )
};

Card.prototype ={
    property: PropTypes.object.isRequired
}
export default Card;

